namespace $ {
	export class $visavis_plot_pie extends $visavis_plotly {
		
		/**
		 * ```tree
		 * plot_raw $visavis_plot_raw
		 * ```
		 */
		@ $mol_mem
		plot_raw() {
			const obj = new this.$.$visavis_plot_raw()
			
			return obj
		}
		
		/**
		 * ```tree
		 * pie_click? null
		 * ```
		 */
		@ $mol_mem
		pie_click(next?: any) {
			if ( next !== undefined ) return next as never
			return null as any
		}
		
		/**
		 * ```tree
		 * colorset /
		 * 	\#3e3f95
		 * 	\#c00
		 * 	\#FE9A2E
		 * 	\#090
		 * 	\#f0f
		 * 	\#09f
		 * 	\#666
		 * 	\#0f3
		 * 	\#0ff
		 * 	\#90c
		 * ```
		 */
		colorset() {
			return [
				"#3e3f95",
				"#c00",
				"#FE9A2E",
				"#090",
				"#f0f",
				"#09f",
				"#666",
				"#0f3",
				"#0ff",
				"#90c"
			] as readonly any[]
		}
	}
	
}

